import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import first from '@/components/first'
import two from '@/components/two'

import tianmao from '@/components/tianmao'
import taobao from '@/components/taobao'
import baidu from '@/components/baidu'

import VueResource from 'vue-resource'//网络请求
import VueTouch from 'vue-touch'//左右滑动事件
Vue.use(VueTouch,{name:'v-touch'})
Vue.use(Router)
Vue.use(VueResource)//注册实例
Vue.component('header-item',{
  props:['message','backUrl'],
  template:`<header class="header">
          <a :href="backUrl"></a> <div><span>{{message}}</span></div>
          </header>`
})
Vue.component('footer-item',{
  props:['message'],
  template:`<footer class="footer">
            <div><span>2018年10月22日-著</span></div> 
          </footer>`
})
export default new Router({
	mode:"history",//去除/#
  routes: [
    {
      path: '/',
      component: index,
//       children:[
//         {
//           path: 'baidu',
//           name: 'baidu',
//           component: baidu
//         },
//         {
//           path: 'taobao',
//           name: 'taobao',
//           component: taobao
//         },
//         {
//           path: 'tianmao',
//           name: 'tianmao',
//           component: tianmao
//         },
//       ]
    },
    {
      path: '/first',
      name: 'first',
      component: first,
      
    },
    {
      path: '/two',
      name: 'two',
      component: two
    },
    {
    	path: '/taobao',
    	name: 'taobao',
    	component: taobao
    },
    
  ]
})



