<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <ol>
      <li v-for="item in testArr">
        {{item.name}}
      </li>
    </ol>
  </div>
</template>

<script>
export default {
  name: 'two',
  data () {
    return {
      msg: 'two.vue',
      testArr:[
              {name:"vue生产环境部署",url:"https://cn.vuejs.org/v2/guide/deployment.html"},
              {name:"百度",url:"https://www.baidu.com/"}
          ],
  
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



