<template>
  <div class="content tabs-content">
    <h1>百度</h1>
     <h1>{{}}</h1>
  </div>
</template>
<script>
export default {
    name: 'baidu',
    data(){
      return {
        name:"name",
      }
        
    },
    computed:{
      fullName:function(){
          var that = this;
          var num = 1;
          setTimeout(function(){
            that.name = that.name + (num+1);
          },1000);
        return that.name;
      },
    },
}
</script>



