<template>
  <div class="content">
<!-- 		<div class="swiper-inner">
			<v-touch v-on:swipeleft='onSwipeLeft()' v-on:swiperight='onSwipeRight()' class="wrapper">
				<div class="item">
					<img :src="swipeImgList[i]"/>
				</div>
				<div class="dot-inner">
					<div v-for="(arr,index) in swipeImgList"  class="dot" :class="idot==index?'active':''"></div>
				</div>
			</v-touch>
		</div> -->
		<!-- 运行模式配置 -->
		<div class="box">
			<h4 class="top-head">运行模式配置</h4>	
			<div class="center-body">
				<input type="hidden"  v-model="old_mode"/>
				<div class="radio-inner">
					<label for="rdo0">
						<span class="radio-text">安装模式</span>
						<input type="radio" class="a-radio" name="mode" id="rdo0" v-model="mode" value="0" />
						<span class="b-radio"></span>
					</label>
				</div>
				<div class="radio-inner">
					<label for="rdo1">
						<span class="radio-text">工作模式</span>
						<input type="radio" class="a-radio" name="mode" id="rdo1" v-model="mode" value="1" />
						<span class="b-radio"></span>
					</label>
				</div>
			</div>
			<h5 class="bot-info">注意：模式修改之后设备将会重新启动</h5>
		</div>
		<!-- 设备ID配置 -->
		<div class="box">
			<h4 class="top-head">设备ID配置</h4>	
			<div class="center-body">
				<div class="radio-inner">
					<div class="item">
						<p class="input-left">设备ID</p>
						<div class="input-right">
							<input placeholder="请输入" type="text" name="device_id" id="device_id" v-model="devId">
						</div>
					</div>		
				</div>
			</div>
			<h5 class="bot-info">注意：设备ID请参见机壳电源侧条码</h5>
		</div>
  </div>
</template>
<style lang="scss">
	
</style>
<script>
export default {
  name: 'index',
  data () {
    return {
			devId: '',
			mode: '',
			direction: '',
			picMode: '',
			mnc: '',
			ssid: "", //wifi热点名称
			dialog: {},
			old_mode:"",
			ir_type:"",//红外传感器形状value
    }
  },
	created() {
		//setInterval(this.onSwipeLeft, 2000);
	},
  mounted(){
    this.$http.get(this.$config.getPicModeUrl(),"").then(function(res){
      //成功回调函数
			var elv = this.$utils.parseINIStr(res).mx6s_conf;
			var pic = elv.pic_upload_mode;
			if (pic == 0) {
				vue.picMode.checked = true;
				vue.picMode = 0;
			} else {
				vue.picMode.checked = true;
				vue.picMode = 1;
			}
			vue.devId = elv.device_id;//系统配置ID
       console.log('获取数据成功---'+res);
    },function(){
      //失败回调函数
        console.log('返回数据失败');
    })
  },
	methods:{
		onSwipeLeft:function(){
			if(this.i<this.swipeImgList.length-1){
				this.i++;
				this.idot++;
			}
			console.log("onSwipeLeft");
		},
		onSwipeRight:function(){
			if(this.i==0){
				return false;
			}else{
				this.i--;
				this.idot--;
			}
			console.log("onSwipeRight");
		},
	},
  computed:{
      reversedMessage(){
          if(typeof this.msg==="String"){
            return (this.msg = "1028");
          }else{
            return this.msg.split("").reverse().join('');
          }
          
      }
  },
}
// $(function(){
// 	$(".test").click(function(){
// 		alert("1")
// 	})
// })

</script>


